
import Loading from 'vux/src/components/loading/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import Lib from 'assets/js/Lib'

let page
export default {
directives: {
TransferDom
},
components: {
Loading,
XInput,
XButton,
Group,
Cell
},
props: {
debounce: Number
},
data () {
return {
receiptNumber: '',
iconType: '',
errorTxt: '',
disableIs: true,
loadingtxt: '',
showLogo: true,
show1: false,
text1: '请稍等'
}
},
created () {
page = this
},
mounted () {
const flag = Lib.U.IsPC()
const bodyC = document.getElementsByTagName('body')[0]
const location = window.location.hostname
if (flag) {
if (location === '193.0.1.245') {
bodyC.className = 'pcBg anXian'
} else {
bodyC.className = 'pcBg sanJiang'
}
} else {
if (location === '193.0.1.245') {
bodyC.className = 'anXian'
} else {
bodyC.className = 'sanJiang'
}
}
window.onscroll = function () {
const t = document.documentElement.scrollTop || document.body.scrollTop
if (t > 0) {
page.showLogo = false
} else {
page.showLogo = true
}
}
},
methods: {
onChange () {
if (page.receiptNumber === '' || page.receiptNumber.length !== 22) {
page.iconType = 'error'
page.errorTxt = '小票号不能为空，且只能为22位数字'
page.disableIs = true
} else {
page.iconType = ''
page.errorTxt = ''
page.disableIs = false
}
},
clickReceipt () {
page.show1 = true
Lib.M.ajax({
'url': '/invoice/orderSerach?searchNo=' + page.receiptNumber,
'success': function (res) {
if (res.code === 0) {
window.location.href = '/e-Invoice/details.html?searchNo=' + page.receiptNumber
} else {
/* page.errorTxt = res.message*/
window.location.href = '/e-Invoice/error.html?searchNo=' + page.receiptNumber
}
setTimeout(() => {
page.show1 = false
}, 1000)
}
})
}

}
}




