const Rxports = {
  IsPC: function () {
    const userAgentInfo = navigator.userAgent
    const Agents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPod']
    let flag = true
    for (var v = 0; v < Agents.length; v++) {
      if (userAgentInfo.indexOf(Agents[v]) > 0) {
        flag = false
        break
      }
    }
    return flag
  },
  setClass: function () {
    const location = window.location.hostname
    const bodyC = document.getElementsByTagName('body')[0]
    if (location === '193.0.1.245') {
      bodyC.className = 'pcBg anXian'
      if (document.getElementById('company') !== null) {
        document.getElementById('company').innerHTML = '安鲜生活'
      }
    } else {
      bodyC.className = 'pcBg sanJiang'
      if (document.getElementById('company') !== null) {
        document.getElementById('company').innerHTML = '三江购物'
      }
    }
  }
}
export default Rxports



